import React, { Component } from 'react';

import Navbar from '../components/navbar/';
import Content1 from '../components/content1';
import Content2 from '../components/content2';


class index extends Component {
    render() {
        return (
            <div>
                <Navbar/>
                <Content1/>
                <Content2/>
            </div>
        );
    }
}

export default index;